body {
  margin: 0 auto;
  background-color: rgba(62.7, 57.3, 65.9, 0.3);
  width: 100%;
}
/* Header styling*/
header {
  background-color: indigo;

  height: 100px;
  display: flex;
  align-items: center;
  width: 100%;
  border-radius: 4px;
}

header > img {
  height: 80%;
  margin-left: 30px;
}

header > p {
  margin-left: 30px;
  font-size: 3em;
  color: white;
  font-family: "Courier New", Courier, monospace;
}

@media only screen and (max-width: 600px) {
  header {
    font-size: 0.8em;
    padding: 0;
  }
  header > img {
    height: 50%;
    margin-left: 30px;
  }
}

/* Form styling*/
.memeForm {
  height: 3.5vh;
  display: flex;
  text-align: center;
  justify-content: center;
  flex-wrap: wrap;
  margin: 15px;
}

.memeForm > input {
  width:20%;
  padding-right:20px;
  font-family: 'Courier New', Courier, monospace;
  font-size: 20px;
  padding: 14px 20px;
  margin: 8px 0;
  border: 1px solid grey;
  border-radius: 4px;
  cursor: pointer;
} 

.memeForm > button {
  background-color: rgba(150, 105, 182, 0.6);
  color: #676570;
  padding: 2px;
  border-radius: 2px;
  border-color: transparent;
  margin-left: 2px;
  font-family: VT323, monospace;
  font-size: 1em;
  letter-spacing: 0.5px;
}

button:hover {
  background-color: rgba(64, 122, 191, 0.5);
  transform: scale(1.1);
  transition: all 0.5s ease-in-out;
}

@media only screen and (max-width: 600px) {
  .memeForm {
    font-size: 0.8em;
    padding: 0;
  }
  .memeForm > input {
    padding: 1px;
    border-radius: 6px;
    width: 25%;
  }
  .meme > button {
    width: 10%;
  }
}

/* Meme image styling*/
.meme {
  height: 80vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.meme {
  position: relative;
  width: 90%;
  margin: auto;
  height: 80vh;
  display: flex;
  justify-content: center;
}
