  bottom: 120px;
}

.meme > h2 {
  position: absolute;
  width: 80%;
  left: 80%;
  transform: translateX(-50%);
  font-family: impact, sans-serif;
  font-size: 2em;
  letter-spacing: 1px;
  color: white;
  text-transform: uppercase;
  margin: 15px 0;
  padding: 0 5px;
  text-shadow: 2px 2px 0 #000, -2px -2px 0 #000, 2px -2px 0 #000,
    -2px 2px 0 #000, 0 2px 0 #000, 2px 0 0 #000, 0 -2px 0 #000, -2px 0 0 #000,
    2px 2px 5px #000;
}

@media only screen and (max-width: 600px) {
  .meme > h2 {
    font-size: 1em;
  }
}

/* Footer styling*/
footer {
  width: 100%;
  display: flex;
  bottom: 0;
  background-color: rgba(150, 105, 182, 0.5);
  background-image: linear-gradient(
    to bottom right,
    rgba(64, 122, 191, 0),
    rgba(64, 122, 191, 1)
  );
  display: flex;
  justify-content: center;
  align-content: center;
  border-radius: 4px;
}

footer > p {
  font-family: VT323, sans-serif;
  color: #676570;
  font-size: 1.5em;
  font-weight: 500;
  letter-spacing: 1px;
  margin: 08px 0;
}

@media only screen and (max-width: 600px) {
  footer > p {
    font-size: 1em;
    padding: 0;
  }
}
